.main {
    background: url("./bg.png") no-repeat center center fixed;
    background-size: cover;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    min-height: 500px;
    margin: var(--margin-md) 0px;
    padding: var(--padding-lg);
}

.main__item {
    flex: 1;
    margin: var(--margin-md);
    padding: var(--padding-md);
}
